.textarea-wrap {
  display: grid; }

.textarea-wrap::after {
  content: attr(data-replicated-value) " ";
  white-space: pre-wrap;
  visibility: hidden; }

.textarea-wrap > textarea {
  resize: none;
  overflow: hidden; }

.textarea-wrap > textarea,
.textarea-wrap::after {
  border: 1px solid black;
  padding: 0.5rem;
  font: inherit;
  grid-area: 1 / 1 / 2 / 2; }

.cw-settings {
  display: block;
  width: 250px;
  min-height: 300px;
  background: #354057;
  color: #efefef;
  overflow: auto; }
  .cw-settings .section .title {
    padding: 5px 10px;
    margin-bottom: 5px;
    font-size: 14px;
    font-weight: bold;
    text-transform: uppercase;
    color: #c3c3c3;
    background: #2b2e3d; }
  .cw-settings .section .controls {
    padding: 5px 10px; }
    .cw-settings .section .controls .form-group {
      display: flex; }
      .cw-settings .section .controls .form-group .form-control {
        display: flex;
        flex-direction: column;
        flex: 1 1;
        font-size: 14px;
        margin-bottom: 5px; }
        .cw-settings .section .controls .form-group .form-control label {
          font-size: 13px;
          margin: 5px 0; }
        .cw-settings .section .controls .form-group .form-control > input, .cw-settings .section .controls .form-group .form-control textarea, .cw-settings .section .controls .form-group .form-control select {
          display: block;
          padding: 0.4rem 0.3rem;
          font: inherit; }

.color-picker {
  display: inline-block; }
  .color-picker .color-box {
    display: inline-block;
    padding: 3px;
    background: #fff;
    border-radius: 2px;
    box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1);
    inset: 0px;
    cursor: pointer; }
    .color-picker .color-box .color-fill {
      width: 30px;
      height: 30px;
      background: #354057;
      border-radius: 2px; }
  .color-picker .popup {
    position: absolute;
    z-index: 2; }
    .color-picker .popup .popup-cover {
      position: fixed;
      top: 0px;
      right: 0px;
      bottom: 0px;
      left: 0px; }
    .color-picker .popup.hide {
      display: none; }

/* add css module styles here (optional) */
.canvas-workflow {
  display: flex; }
  .canvas-workflow .cw-wrapper {
    flex: 1 1 auto;
    margin: 0;
    overflow: auto; }
    .canvas-workflow .cw-wrapper canvas {
      display: block;
      -webkit-user-select: none;
         -moz-user-select: none;
          -ms-user-select: none;
              user-select: none; }
